













































import { defineComponent, PropType } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import { ActionLinkData } from '@/app/shared/client/type/action-link';
import useI18N from '@/app/shared/client/logic/i18n';

const ActionLinkContentEditor = defineComponent({
  components: {
    ArrowRightIcon: Icon.ArrowRightIcon,
    PlusIcon: Icon.PlusIcon,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<ActionLinkData>,
    },
  },
  setup(_, { root }) {
    const i18n = useI18N(root.$store);

    return {
      i18n,
    };
  },
});

export default ActionLinkContentEditor;
